
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: -webkit-gradient(linear, right top, left top, from(rgba(7,27,82,1)), to(rgba(0,128,128,1)));
  background: linear-gradient(to left, rgba(7,27,82,1) 0%, rgba(0,128,128,1) 100%); /* w3c */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


@font-face {
    font-family: 'SEGA LOGO FONT';
    font-style: normal;
    font-weight: normal;
    src: local('SEGA LOGO FONT'), url(/RobotCard-react/static/media/SEGA.12a3db8b.woff) format('woff');
}

h1 {
    font-family: 'SEGA LOGO FONT';
    font-weight: 200;
    color: #0ccac4;
}
h3{
    font-family: 'SEGA LOGO FONT';
    font-weight: 100;
    color: #0ccac4;
}
h5{
    font-family: 'SEGA LOGO FONT';
    font-weight: 100;
    color: #0ccac4;
}

.Card_disabledLink__1CX_t {
  pointer-events: none;
}




.pushable:not(body) {
    -webkit-transform: none;
            transform: none;
}

.pushable:not(body) > .ui.sidebar,
.pushable:not(body) > .fixed,
.pushable:not(body) > .pusher:after {
    position: fixed;
}
